export const checkItem = (array, id) => {
  return array.find((itemdata) => itemdata.id === id);
};

export const ProductList = () => {
  const { dataDispatch, wishList, cartItems } = useData();
  const {
    showInventory,
    showFastDelivery,
    sortBy,
    productDispatch
  } = useProductData();
  const getSortedData = (productList, { sortBy }) => {
    if (sortBy === "PRICE_LOW_TO_HIGH") {
      return productList.sort((a, b) => a["price"] - b["price"]);
    }
    if (sortBy === "PRICE_HIGH_TO_LOW") {
      return productList.sort((a, b) => b["price"] - a["price"]);
    }
    return productList;
  };
  const getFilteredData = (
    productList,
    { showInventory, showFastDelivery }
  ) => {
    return productList
      .filter(({ inStock }) => (showInventory ? inStock : true))
      .filter(({ fastDelivery }) => (showFastDelivery ? fastDelivery : true));
  };
  const sortedData = getSortedData(productDB, { sortBy });
  const filteredData = getFilteredData(sortedData, {
    showInventory,
    showFastDelivery
  });
  console.log(filteredData);
  return (
    <>
      {/*<input
        style={{ marginTop: "1rem" }}
        placeholder="search products by name"
        onChange={(event) =>
          dataDispatch({ type: "SEARCH", payload: event })
        }
      />*/}
      <fieldset className="fieldset">
        <label>
          <input
            type="radio"
            name="sort"
            checked={sortBy === "PRICE_LOW_TO_HIGH"}
            onClick={() =>
              productDispatch({ type: "SORT_BY", payload: "PRICE_LOW_TO_HIGH" })
            }
          />
          Price low to high
        </label>
        <label>
          <input
            type="radio"
            name="sort"
            checked={sortBy === "PRICE_HIGH_TO_LOW"}
            onClick={() =>
              productDispatch({ type: "SORT_BY", payload: "PRICE_HIGH_TO_LOW" })
            }
          />
          Price high to low
        </label>
      </fieldset>
      <label>
        {" "}
        <input
          type="checkbox"
          checked={showInventory}
          onClick={() => productDispatch({ type: "TOGGLE_INVENTORY" })}
        ></input>
        Include instock
      </label>
      <label>
        <input
          type="checkbox"
          checked={showFastDelivery}
          onClick={() => productDispatch({ type: "SHOW_FAST_DELIVERY" })}
        />
        Fast Delivery
      </label>
      <div>
        <button
          style={{ margin: "1rem" }}
          onClick={() => productDispatch({ type: "CLEAR" })}
        >
          Clear
        </button>
      </div>
      <h2>Products</h2>
      <ul>
        {filteredData.map((item) => (
          <div className="border-div shadow">
            <div className="flexing">
              <h3 className="heading align-center">{item.name}</h3>
              <img className="img" src={item.image} alt="productname" />
              <p>Rs.{item.price}</p>
              {item.inStock ? <div>in stock</div> : <div>out of stock</div>}
              {item.fastDelivery ? (
                <div>Fast Delivery</div>
              ) : (
                <div>5 days minimum</div>
              )}
              <Link
                to={checkItem(cartItems, item.id) ? "/cart" : "/"}
                style={{ textDecoration: "none" }}
                onClick={() => {
                  if (!checkItem(cartItems, item.id)) {
                    dataDispatch({
                      type: "ADD_TO_CART",
                      payload: item
                    });
                  }
                }}
              >
                <button>
                  {checkItem(cartItems, item.id) ? (
                    <div>Go to cart</div>
                  ) : (
                    <div>Add to cart</div>
                  )}
                </button>
              </Link>
              <svg
                viewBox="0 0 24 24"
                style={{
                  fill: `${checkItem(wishList, item.id) ? "red" : "black"}`
                }}
                onClick={() => {
                  checkItem(wishList, item.id)
                    ? dataDispatch({
                        type: "REMOVE_FROM_WISHLIST",
                        id: item.id
                      })
                    : dataDispatch({ type: "ADD_TO_WISHLIST", payload: item });
                }}
                class="icon-button"
              >
                <g>
                  <path d="M12 21.638h-.014C9.403 21.59 1.95 14.856 1.95 8.478c0-3.064 2.525-5.754 5.403-5.754 2.29 0 3.83 1.58 4.646 2.73.814-1.148 2.354-2.73 4.645-2.73 2.88 0 5.404 2.69 5.404 5.755 0 6.376-7.454 13.11-10.037 13.157H12zM7.354 4.225c-2.08 0-3.903 1.988-3.903 4.255 0 5.74 7.034 11.596 8.55 11.658 1.518-.062 8.55-5.917 8.55-11.658 0-2.267-1.823-4.255-3.903-4.255-2.528 0-3.94 2.936-3.952 2.965-.23.562-1.156.562-1.387 0-.014-.03-1.425-2.965-3.954-2.965z"></path>
                </g>
              </svg>
            </div>
          </div>
        ))}
      </ul>
    </>
  );
};
